import Link from 'next/link'

const linkStyle = {
  marginRight: 15
}

const Header = () => (
  <div>
    <nav>
      <a className="logo">Mindful Design</a>
      <ul className="links">
        <li>The book</li>
        <li>The talk</li>
        <li>The workshop</li>
      </ul>
    </nav>
    <style jsx>{`
      nav {
        max-width: 1020px;
        margin: auto;
        display: grid;
        grid-template-columns: 200px 1fr;
      }
      .logo {
        font-family: gastromond, serif;
        font-size: 1.2rem;
      }
    `}</style>
  </div>
)

export default Header